'use strict';

/**
 * @file    
 *          
 * */

/**
 * @summary     
 * @function    throttle
 * @memberOf    maple.util
 * @param       {Function}  func
 * @param       {Number}    wait
 * @param       {Function}  [cancelCB]  
 * @param       {Boolean}   [leading]    true 
 * @return      {Function}
 * @desc        
 * */
let throttle = function(func, wait, cancelCB, leading){
	let timeout = null
		, lastTrigger = false
		, lastTriggerOpts = null
		, result = function(){
			let that = this || null
				, argv = [].slice.call( arguments )
				;

			if( !timeout ){
				func.apply(that, argv || []);

				timeout = setTimeout(function(){
					clearTimeout( timeout );
					timeout = null;

					if( lastTrigger ){  // 
						result.call(lastTriggerOpts.that, lastTriggerOpts.argv);

						lastTrigger = false;
	                    lastTriggerOpts = null;
					}
				}, wait);
			}
			else{   // 
				if( leading ){
					lastTrigger = true;

					lastTriggerOpts = {
						that
						, argv
					};
				}
			}
		}
		;

	if( typeof cancelCB === 'boolean' ){
		leading = cancelCB;

		cancelCB = null;
	}

	/**
	 * 
	 * */
	result.cancel = function(){
		if( timeout ){
			clearTimeout( timeout );
			timeout = null;
		}

		lastTrigger = false;
		lastTriggerOpts = null;

		cancelCB && cancelCB();
	};

	/**
	 * 
	 * @param   {Object}    [context=null]   this 
	 * @param   {Array}     [argv=[]]       
	 * */
	result.immediate = function(context=null, argv=[]){
		result.cancel();

		lastTrigger = false;
		lastTriggerOpts = null;

		result.call(context, argv);
	};

	return result;
};

export default throttle;