'use strict';

/**
 * @file     
 * */

/**
 * @summary      
 * @function    classify
 * @memberOf    maple.util
 * @param       {Object[]}  target
 * @param       {String}    key
 * @return      {Object}
 * @desc         key
 * */
let classify = function(target, key){
	return target.reduce((all, d)=>{
		if( d[key] ){

			if(!(d[key] in all) ){
				all[d[key]] = [];
			}

			all[d[key]].push( d );
		}

		return all;
	}, {});
};

export default classify;